.workout-list{
	margin: 100px 0;
	border: 3px solid black;
	width: 750px;
	height: auto;
	border-radius: 20px;
	padding: 20px;
	font-size: 23px;
}

.wrapper{
	margin: 100px 250px;
}

.title{
	display: flex;
	font-size: 23px;	
}

.title-date{
	margin-bottom: 10px;

}

.title-distance{
	margin-left: 110px;
	margin-bottom: 10px;
}

input{
margin-right: 30px;
font-size: 20px;
height: 50px;
border: 3px solid black;
border-radius: 10px;
}

.add-workout{
	height: 70px;
	width: 100px;
	border: 3px solid black;
	border-radius: 10px;
	font-size: 17px;
}
.no-workout{
	opacity: 0.6;
}

.workout-list-row{
	display:flex;
	align-items: center;
	justify-content: space-between;
}

.button-delete{
	cursor: pointer;
}